<template>
  <div class="home">
    <vue-scroll :ops="ops">
      <div class="box">
        <article-item class="articleitem" v-for="(item,index) in data" :key="index" :article="item" @click.native="handlerarticle(item._id)"></article-item>
      </div>
      <div class="page">
        <el-pagination
          background
          layout="prev, pager, next"
          @current-change="handleCurrentChange"
          :page-size="pageSize"
          :total="total"
        ></el-pagination>
      </div>
    </vue-scroll>
  </div>
</template>
<script>
import ArticleItem from "./ArticleItem";
import { GetSinglePage } from "../../api/index";
export default {
  data() {
    return {
      ops: {
        //vuescroll的配置
        vuescroll: {
          sizeStrategy: "percent",
          detectResize: true
        },
        scrollPanel: {
          scrollingX:false
        },
        rail: {},
        bar: {
          showDelay: 500,
          onlyShowBarOnScroll: false,
          keepShow: false,
          background: "#b3c0d1",
          opacity: 1,
          hoverStyle: false,
          specifyBorderRadius: false,
          minSize: false,
          size: "6px",
          disable: false
        }
      },
      total: null,
      pageSize: 12,
      data: []
    };
  },
  created() {
    this.getData(1);
  },
  components: {
    ArticleItem
  },
  methods: {
    handleCurrentChange(val) {
      this.getData(val);
    },
    getData(page) {
      GetSinglePage({ page, limit: this.pageSize }).then(res => {
        this.data = [];
        this.total = res.count;
        this.data = res.data;
      });
    },
    handlerarticle(id){
      this.$router.push({ name: 'article',query: { id}})
    }
  }
};
</script>
<style lang="scss" scoped>
.home {
  width: 100%;
  height: calc(100% - 20px) !important;
}
.box {
  width: calc(100% - 100px) !important ;
  height: auto !important;
  margin: 10px 50px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
.box:after {
  content: "";
  width: 300px;
  border: 1px solid transparent;
}
.articleitem {
  transition: 1s;
}
.articleitem:hover {
  cursor: pointer;
  box-shadow: 0 10px 8px rgba(10, 16, 20, 0.24), 0 0 8px rgba(10, 16, 20, 0.12);
  // box-shadow: 0px 10px 10px rgba(0,0,0,.2);
}
.page {
  width: 100%;
  text-align: center;
}
</style>